.pagginationContainer{
 display: flex;
    justify-content: center;
    width: 50%;
    margin: 20px auto;
}
.page{
    display: inline-block;
    color: #6AC045;
    margin: 5px;
    padding: 5px;
    cursor: pointer;
    background-color: #1d1d1d;
    font-weight: bold;
}
.page.active{
    border: 2px solid #9acc1c ;
}
.page:hover{
    color: #9acc1c;
   }
.page.invisible{
    display: none;
}